.Image {
	text-align: center;
  }
  
  .content {
	margin: 20px;
  }
  
  .image-space {
	width: 300px;
	height: 300px;
	background-color: #f0f0f0;
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 0 auto;
  }
  
  .image-space img {
	max-width: 100%;
	max-height: 100%;
  }
  
  .name {
	font-size: 24px;
	margin: 20px 0;
  }
  
  .description {
	font-size: 16px;
	color: #555;
  }